import React from 'react'
import CoverVid from '../Assets/TitleVid.mp4'
export default function Title() {
    return (
        
        <div>
            
            <div className="container">
            <video loop autoPlay src={CoverVid} className='video-background'></video>
            
                <div className='hero-medium'>
                    <div className='container-item'>
                        
                        <h1 className="title">
                        Hello, I'm Ahmed Zaher
                        </h1>
                        <h1 className="title">
                        A Web Developer and Designer
                        </h1>
                    </div>
                </div> 
            </div>
            
            
        </div>
    )
}
